cope="col"> ()</th></tr></thead><tbody><template v-for="item,i in docs"><tr class="text-center"><th><input type="checkbox" v-model="selected" :value="item._id"><i class="fas fa-print text-success" v-if="item.isPrint" data-toggle="tooltip" data-placement="top" title="!"></i></th><th scope="row">{{(page*show)+(i+1)}}</th><td class="text-start"><a class="card-link" :href="`/portal/invoice/form/${item._id}?searchparam=${JSON.stringify(search)}`">{{item.sequence}}</a></td><td class="text-start"><a class="card-link" :href="`/portal/meter/history/${item.meter}`">{{item.meter}}</a></td><td class="text-center">{{item.category}}</td><td class="text-center"> <template v-if="item.month!=undefined&amp;&amp;item.year!=undefined"><span>{{getThaiMonth(item.month)}}{{(item.year).toString().substring(2, 4)}}</span></template></td><td class="text-start">{{item.name}}</td><td>{{item.address}}</td><td class="text-end"> <span>{{(item.debtAmount??0).formatFull()}}</span></td><td class="text-end">{{(item.qty??0).formatFull()}}</td><td class="text-end">{{(item.rate??0).formatFull()}}</td><td>{{item.categoryType}}</td><td class="text-end">{{(item.totalAmount??0).formatFull()}}</td><td class="text-end">{{(item.vat??0).formatFull()}}</td><td class="text-end">{{((item.totalAmount??0)+(item.vat??0)).formatFull()}}</td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}} </td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)"></a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)"></a></li></ul></nav></div>