<div class="row"><div class="col-4"><label class="mt-3"></label><select id="year-select" v-model="search.year"> <option selected="" :value="0"></option><template v-for="item,i in 2"><option>{{((new Date().getFullYear()+543)-i) + 2}}</option></template><template v-for="item,i in 10"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-4"><label class="mt-3"></label><select id="quarter-select" v-model="search.quarter"> <option value="1">1</option><option value="2">2</option><option value="3">3</option><option value="4">4</option></select></div><div class="col-4"> <label class="mt-3"></label><select id="month-select" v-model="search.month"> <option selected="" :value="0"></option><option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div><div class="col-12 my-3"><label></label><select id="area-select" v-model="search.area"><option selected=""></option><template v-for="area in areas"><option :value="area._id">{{area.name}}</option></template></select></div><div class="d-flex justify-content-end"><button class="btn btn-outline-success mx-2" type="submit" @click="clear()"></button><button class="btn btn-outline-success" @click="$emit('search')"></button></div></div>