<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4"></h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span> Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><div class="row d-print-none"><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.month"> <option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.year"> <option selected="" :value="0"></option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div></div><div class="row d-print-none"><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected=""></option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected=""></option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="4,"></option></select></div></div><div class="row justify-content-between mt-5 table-print"><div class="col-8"><h5> ( Vat)  <span class="text-primary fw-bold">[ {{getThaiMonth(search.month)}}{{search.year}} ]</span></h5></div><div class="col-4"><h5 class="text-end"> <span class="text-primary">{{new Date(this.Date).toThaiShort()}}</span></h5></div></div><div class="row justify-content-between"><div class="col-8"><h5>: <span> </span><span class="text-primary">({{(rates.find(el=>el.code==search.code)??{}).name}})</span></h5></div><div class="col-4"><h5 class="text-end fw-bold">:</h5></div></div><div class="row justify-content-between"><div class="col-8"><h5>: <span>333  1  23     10900 02-273-8530-9</span></h5></div><div class="col-4"><h5 class="text-end fw-bold">: <span class="text-primary">{{search.cat}}</span></h5></div></div><div class="row justify-content-between mb-5"><div class="col-8"><h5 class="fw-bold">: <span class="text-primary fw-bold">0994000203021</span></h5></div><div class="col-4"><h5 class="text-end">: []</h5></div></div><div class="row table-print"><div class="col"><span> </span><span class="text-primary">{{docs.map(el=>((el.debtAmount)*(el.vat??0)+(el.rate * el.qty))).reduce((a,b)=>a+b,0).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary">{{docs.map(el=>((el.totalAmount)*(el.vat??0)+(el.rate * el.qty))).reduce((a,b)=>a+b,0).formatFull()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary">{{(totalAmount??0).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary">{{docs.map(el=>((el.paymentAmount))).reduce((a,b)=>a+b,0).formatFull()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary">{{docs.map(el=>el.vat??0).reduce((a,b)=>a+b,0).formatFull()}}</span><span></span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" colspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2">/</th><th class="align-middle text-end" scope="col" rowspan="2"> 7% ()</th><th class="align-middle text-end" scope="col" rowspan="2"> 7% ()</th><th class="align-middle text-end" scope="col" rowspan="2"> ()</th><th class="align-middle text-end" scope="col" rowspan="2"> ()</th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th></tr><tr class="text-center"><th>  </th><th></th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}  </th><td>{{toThaiFormat(item.paymentDate)}}</td><td class="text-start">{{item.sequence}}</td><td class="text-start"> {{item.meter}}</td><td class="text-start"> {{item.prefix??""}} {{item.name??"-"}}</td><td class="text-end">{{((item.debtAmount)*(item.vat??0)+(item.rate*item.qty)).formatFull()}}</td><td class="text-end">{{((item.totalAmount??0)*(item.vat??0)+(item.rate*item.qty)).formatFull()}}</td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}}</td><td class="text-end">{{(item.paymentAmount).formatFull()}}</td><td class="text-end">{{(((item.invoiceAmount??0)/1.07)*0.07).formatFull()}}</td><td>{{item.description}}</td></tr></tbody></table></div></div></div>