<div class="container-fluid p-4"><h2 class="text-decoration-underline"></h2><div class="col"><label class="text-muted">()</label></div><div class="row"><div class="col-6"><label class="form-label">:</label><select class="form-select form-select-xs mb-3" v-model="search.isPaid"><option selected=""></option><option :value="true"></option><option :value="false"></option></select></div><div class="col-6"><label class="form-label"></label><input class="form-control" v-model="search.text"></div></div><div class="row"><div class="col-6"><label class="mt-3">:</label><select class="form-select form-select-xs mb-3" v-model="search.year"><option selected="" :value="0"></option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6">   <label class="mt-3"></label><select class="form-select form-select-xs mb-3" v-model="search.month"><option selected=""></option><option value="1"></option><option value="2"></option><option value="3"></option><option value="4"></option><option value="5"></option><option value="6"></option><option value="7"></option><option value="8"></option><option value="9"></option><option value="10"></option><option value="11"></option><option value="12"></option></select></div><div class="d-flex justify-content-end"><button class="btn btn-outline-success mx-2" type="submit" @click="search={}; fetchData()"></button></div></div><div class="col mt-5"><label class="mb-3"> {{$route.params.id}}</label><label class="mb-3 px-2">{{(docs[0]??{}).name}} </label></div><div class="col d-flex justify-content-end align-items-center mb-2"><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span> Excel </span></button></div><div class="row mt-2"><div class="col"><span></span><span class="text-primary mx-2">{{(docs.length??0).formatComma()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{((docs[docs.length-1]??{debtAmount:0}).debtAmount??0).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{(receipts.length??0).formatComma()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{(docs.map(el=>el.totalAmount).reduce((a,b)=>a+b,0)??0).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{((docs.map(el=>el.vat).reduce((a,b)=>a+b,0)??0)).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{((docs.map(el=>el.totalAmount+el.vat).reduce((a,b)=>a+b,0)??0)).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{(receipts.map(el=>el.paymentAmount).reduce((a,b)=>a+b,0)??0).formatFull()}}</span><span></span></div></div><table class="table table-striped"><thead class="table-dark"><tr class="text-center"><th scope="col" colspan="9"></th><th scope="col" colspan="4" style="border-left:1px solid white;"></th></tr><tr class="text-center"><th></th><th></th><th></th><th> (..)</th><th></th><th></th><th> ()</th><th></th><th style="border-left:1px solid white;">   </th><th></th><th></th><th></th></tr></thead><tbody class="text-center"><template v-for="item,i in filtered"><tr><td scope="row">{{i+1}}</td><td>{{item.sequence}}</td><td> <span>{{toThaiFormat(item.year,item.month)}}</span></td><td class="text-end"> <span>{{(item.qty??0).formatFull()}}</span></td><td class="text-end"> <span>{{(item.rate??0).formatFull()}}</span></td><td class="text-end"><span>{{(item.totalAmount??0).formatFull()}}</span></td><td class="text-end"> <span>{{(item.vat??0).formatFull()}}</span></td><td class="text-end"><span>{{((item.totalAmount??0)+(item.vat??0)).formatFull()}}</span></td><td style="border-left:1px solid #AAA;"> <template v-if="findReceipt(item)"><span>{{toThaiFormat((findReceipt(item)??{}).year,(findReceipt(item)??{}).month)}}</span></template><template v-else><span>-  </span></template></td><td><span>{{((findReceipt(item)??{}).sequence??"-")}}</span></td><td> <span>{{(((findReceipt(item)??{}).invoiceAmount??0)+((findReceipt(item)??{}).debtAmount??0)).formatFull()}}</span></td><td> <span>{{((findReceipt(item)??{}).paymentAmount??0).formatFull()}}</span></td></tr></template></tbody></table></div>