<div class="container-fluid p-4"> <h2 class="text-decoration-underline mb-5"></h2><div class="row mt-2"><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center"><th></th><th></th><th></th><th></th></tr></thead><tbody><tr class="text-center" v-for="(item,index) in docs"> <td>{{index+1}} </td><td>{{item.name.replace("Invoice","").replace("Usage","").replace("Receipt"," / ").replace("Payment","")}}</td><td>{{item.year}}</td><td>{{(item.sequence).formatComma()}}</td></tr></tbody></table></div></div></div>