<NoAccess v-if="store.state.access.findIndex(el =&gt; el == '/') ==-1"></NoAccess><div class="container" v-else><h2 class="mt-5"> </h2><span class="text-muted">(-)</span><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><label class="form-label"></label><div class="col-12 py-2"><select class="form-select" v-model="limit"><option :value="0"></option><option>1</option><option>2</option><option>3</option><option>5</option><option>10</option><option>15</option></select></div><div class="row"><div class="table-container-scroll-x"><table class="table table-bordered table-print"><thead class="text-center bg-info"><tr><th rowspan="2"></th><th rowspan="2"></th><th colspan="3" v-for="item in 8"> {{yearLong-8+item}}</th></tr><tr><template v-for="item in 8"><th> ()</th><th> ()</th><th></th></template></tr></thead><tbody><template v-for="doc,i in filtered"><tr class="text-end" v-if="doc.jaiTrongCount&gt;=limit"><td>{{i+1}} </td><td class="text-start">{{doc.area}} <span class="text-muted">({{doc.jaiTrongCount}})</span></td><template v-for="item in 8"><td>{{(getCalc(doc.calculations,1,yearLong-8+item)??0).formatFull()}} </td><td>{{(getColc(doc.collections,1,yearLong-8+item)??0).formatFull()}} </td><td>{{getDate(doc.collections,1,yearLong-8+item)}}</td></template></tr></template></tbody></table></div></div></div>