<div id="myImage"><img class="img-fluid" src="@/assets/background.png"></div><div class="wrapper h-100 d-flex justify-content-center"><form id="formContent" @submit.prevent="getUsers" autocomplete="off"><input autocomplete="false" name="hidden" type="text" style="display:none;"><h2><span class="ms-3" v-if="userTrying">(...)</span></h2><div class="mb-3 mx-5"><input class="form-control" id="exampleInputEmail1" autocomplete="new-user" type="username" placeholder="" v-model="username"></div><div class="mb-3 mx-5"><input class="form-control" id="exampleInputPassword1" autocomplete="new-password" type="password" placeholder="" v-model="password"></div><div class="row"><div class="col"><button class="btn btn-primary mb-3" type="submit"></button><div class="mb-2 mx-3 mb-1 mt-3"><a class="card-link" type="button" data-bs-toggle="modal" data-bs-target="#exampleModalCenter" data-bs-whatever="@mdo">? </a></div></div></div></form></div><div class="modal fade" id="exampleModalCenter"><div class="modal-dialog modal-lg modal-dialog-centered" style="width:30em"><div class="modal-content"><div class="modal-body"><h5 class="text-center"></h5><form><div class="mb-0"><JMInput label="" placeholder="" v-model="email"></JMInput></div></form><div class="d-flex justify-content-center"><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" @click="resetRequest()"></button></div></div></div></div></div>