<div class="container"><div class="row"><h2>  /</h2><h4> </h4><ul class="list-group my-3"><template v-for="item in filtered"> <li class="list-group-item" v-if="item.problemType==''">{{item.area}}</li></template></ul><h4> </h4><ul class="list-group my-3"><template v-for="item in filtered"> <li class="list-group-item" v-if="item.problemType==''">{{item.area}}</li></template></ul><p>  </p></div><table class="table"><thead><tr class="text-center"><th>#</th><th></th><th></th><th></th><th></th><th> <i class="fas fa-ellipsis-h"></i></th></tr><tr class="text-center" v-for="item,i in filtered"><td>{{i+1}}</td><td>{{item.area}}</td><td>{{item.problemType}}</td><td>{{item.problem}}</td><td>{{item.solution}}</td><td><a @click="hideItem(item._id)"> <i class="fas fa-trash-alt text-danger"></i></a></td></tr><tr><td></td><td><select class="form-select" v-model="area"><option selected=""></option><template v-for="area in areas"><option :value="area._id">{{(area.prefix??'')+(area.name??'')}}</option></template></select></td><td> <select class="form-select" v-model="problemType"><option selected=""></option><option></option><option></option><option> </option></select></td><td> <input class="form-control" v-model="problem"></td><td> <input class="form-control" v-model="solution"></td><td class="text-center"><a class="btn btn-outline-success" @click="pushToServer()"></a></td></tr></thead></table></div>