<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='/')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4">/</h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span> Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><pre></pre><div class="row d-print-none"><div class="col-3"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected=""></option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-3"> <label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected=""></option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="4,"></option></select></div><div class="col-3">  <label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.month"> <option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div><div class="col-3"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" v-model.number="search.year"> <option selected="" :value="0"></option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div></div><div class="mt-5 mb-5 table-print">     <h5 class="text-center"> <span class="text-primary">{{(rates.find(el=>el.code==search.code)??{}).name}} </span><span> </span><span class="text-primary">{{search.cat}} </span><span> </span><span class="text-primary">{{getThaiMonth(search.month)}} {{search.year}}</span></h5></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span></span></div></transition></div></div><div class="row mt-2"><div class="col table-print"><span> </span><span class="text-primary mx-2" id="totalCount">{{(totalCount??0).formatComma()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary mx-2">{{((totalAmount??0)+(totalDebt??0)).formatFull()}}</span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary mx-2">{{docs.map(el=>((el.taxDebt??0)+(el.taxAmount))).reduce((a,b)=>a+b,0).formatFull()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span></span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2">-</th><th class="align-middle" scope="col" colspan="1"></th><th class="align-middle text-end" scope="col" rowspan="2"> ()</th><th class="align-middle text-end" scope="col" rowspan="2"> </th><th class="align-middle" scope="col" rowspan="2">..</th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle" scope="col" colspan="1"></th><th class="align-middle text-end" scope="col" rowspan="2"></th><th class="align-middle" scope="col" rowspan="2"></th></tr><tr class="text-center"><th>()</th><th></th></tr></thead><tbody><tr v-for="item,i in docs"><th scope="row">{{i+1}}  </th><td>{{new Date(item.paymentDate).toThaiShort()}}</td><td class="text-start">{{item.sequence}}</td><td class="text-start">{{item.meter}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td class="text-center">{{item.debtText}}</td><td class="o text-end">{{(item.debtAmount).formatFull()}} </td><td class="o text-end"><span>{{item.taxDebt}}</span></td><td class="text-end">{{item.qty}}</td><td class="text-end"><span>{{(item.totalAmount).formatFull()}} </span></td><td class="text-end"><span>{{(item.taxAmount).formatFull()}} </span></td><td class="g text-end"><span>{{(item.totalAmount+item.taxAmount).formatFull()}} </span></td><td class="text-end"><span>{{(item.taxDebt+item.taxAmount).formatFull()}} </span></td><td class="text-end"><span>{{((item.debtAmount+item.totalAmount+item.taxDebt+item.taxAmount)??0).formatFull()}} </span></td><td class="text-end"><span>{{(item.paymentAmount??0).formatFull()}}</span></td><td>{{item.description}}</td></tr></tbody></table></div></div></div>