<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='')==-1"></NoAccess><div class="container-fluid p-4" v-else><button class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#modal" ref="modal" style="display:none;">   </button><div class="modal fade" id="modal" tabindex="-1" role="dialog" aria-labelledby="modal" aria-hidden="true"><div class="modal-dialog modal-dialog-centered" role="document"><div class="modal-content"><div class="modal-body"><label class="form-label"></label><JMCalendar v-model="invoiceDate"></JMCalendar></div><div class="modal-footer"><button class="btn btn-secondary" type="button" data-bs-dismiss="modal" ref="close"></button><button class="btn btn-primary" type="button" @click="postData()"></button></div></div></div></div><StepBilling step="1"></StepBilling><h2 class="text-decoration-underline mb-1"></h2><div class="col"><label class="text-muted">()</label></div><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info mx-3" :to="`/portal/usage/form`"> (Key-In)</router-link><router-link class="btn btn-success" to="/portal/usage/import"> Excel</router-link></div><JMBillingsearch @search="fetchData()" @clear="docs = []; totalCount=0; totalQty=0;" v-model="search" isClear></JMBillingsearch><div class="row"><div class="col-2 d-flex justify-content-start"><div class="row mx-1"><label>   </label><select class="form-select form-select-sm mb-2 mx-2" v-model="show" @change="fetchData() ,clearPage()"><option selected="" :value="999999999"></option><option :value="50">50</option></select></div></div><div class="col-2 d-flex justify-content-start"><div class="row"><label>   </label><select class="form-select form-select-sm mb-2 mx-2" v-model="sort" @change="fetchData()"><option value="{&quot;no&quot;:1}"> Excel</option><option value="{&quot;createdAt&quot;:-1}"></option><option value="{&quot;meter&quot;:1}"> </option><option value="{&quot;meter&quot;:-1}"> </option><option value="{&quot;name&quot;:-1}"> -</option><option value="{&quot;name&quot;:1}"> -</option><option value="{&quot;address&quot;:-1}"> -</option><option value="{&quot;address&quot;:1}"> -</option></select></div></div></div><div class="col d-flex justify-content-end mb-2"><button class="btn btn-danger text-white mx-3" v-if="selected.length != 0" type="button" @click="deleteMany()"> </button><div class="row"><button class="btn btn-outline-success text-dark" type="button" :disabled="(selected.length == 0)" @click="downloadExcel()"> <span> Excel ({{selected.length}}) </span></button><button class="btn btn-primary text-white" type="button" :disabled="(selected.length == 0)" @click="showModal()"> <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true" v-if="working"></span><span> ({{selected.length}}) </span></button></div></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span></span></div></transition></div></div><div class="row mt-2"><div class="col"><span> </span><span class="text-primary" id="totalCount">{{(totalCount??0).formatComma()}} </span><span> </span><span class="mx-3">|</span><span> </span><span class="text-primary">{{(totalQty??0).formatFull()}}</span><span> ..</span><span class="mx-3">|</span><span>: </span><span class="text-primary">{{getThaiMonth(latest.month)}} {{(latest.year??0).toString().substring(2, 4)}}</span></div><div class="col-12 p-2"><table class="table table-striped table-hover"><thead><tr class="table-dark text-center align-middle"><th><div class="row"><input type="checkbox" @change="selectAll($event)" v-model="isSelectedAll"></div></th><th></th><th></th><th></th><th></th><th class="text-end"> (..)</th><th></th><th></th><th></th></tr></thead><tbody class="align-middle text-center"><template v-for="item,i in docs"><tr><th><div class="row"><input type="checkbox" v-model="selected" :value="item._id"><i class="fas fa-receipt text-success mt-2" v-if="item.isNextStage" data-toggle="tooltip" data-placement="top" title="!"></i><i class="fas fa-print text-success mt-2" v-if="item.isPrint" data-toggle="tooltip" data-placement="top" title="!"></i></div></th><th scope="row">{{(page*show)+(i+1)}}</th><td class="text-start"><router-link class="card-link" :to="`/portal/meter/history/${item.meter}`">{{item.meter}}</router-link></td><td class="text-start">{{item.name}}</td><td>{{item.category}} </td><td class="text-end">{{(item.qty??0).formatFull()}}</td><td>{{getThaiMonth(item.month)}}{{(item.year??0).toString().substring(2, 4)}}</td><td><router-link class="btn btn-info" :to="`/portal/edit/history/${item._id}`"> <i class="class fas fa-history"></i></router-link></td><td><router-link class="btn btn-warning" v-if="item.isPrint" :to="`/portal/usage/edit/${item._id}`"></router-link><router-link class="btn btn-warning" v-else :to="`/portal/usage/edit/${item._id}?searchparam=${JSON.stringify(search)}`"></router-link></td></tr></template></tbody></table></div></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)"></a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)"></a></li></ul></nav></div>