<NoAccess v-if="store.state.access.findIndex(el =&gt; el == '') ==-1"></NoAccess><div class="container" v-else><div class="d-flex justify-content-end my-3 d-print-none" v-if="this.$route.path != '/portal/income/dashboard'"><button class="btn btn-primary text-white" @click="print()"></button></div><div class="row table-print"><div class="d-flex justify-content-between"><h2></h2><span>: </span></div><table class="table"><thead><tr><th>#</th><th></th><th class="text-end" v-for="item in groupped"> {{item.budgetYear}}</th></tr></thead><tbody><tr><td>1</td><td></td><td class="text-end" v-for="item in groupped"><span>{{(item.sum??0).formatMB()}}</span></td></tr><tr><td>2</td><td></td><td class="text-end" v-for="item,i in groupped"><span>{{((item.sum??0)-((groupped[i-1]??{}).sum??0)??0).formatMB()}}</span></td></tr><tr><td>3</td><td></td><td class="text-end" v-for="item,i in groupped"><span>{{(getPercent(((item.sum??0)-((groupped[i-1]??{}).sum??0)??0),((groupped[i-1]??{}).sum??0))??0).toFixed(2)}}</span></td></tr></tbody></table></div><div class="card dashboard-card mx-3 mt-5 col-12"><div class="card-body row"><canvas ref="chart1"></canvas><hr class="mt-3"><p class="card-text">/</p></div></div></div>