<div class="container-fluid p-4"><form class="col-12" v-on:submit.prevent="onSubmit"><h2 class="text-decoration-underline mb-1" v-if="this.$route.params.id == undefined"></h2><h2 class="text-decoration-underline mb-1" v-else></h2><div class="col mb-4"><label class="text-muted">()</label></div><div class="mb-3"><JMCalendar label="" v-model="doc.paymentDate"></JMCalendar></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="number" v-model="doc.invoiceNumber" @input="checkNumber()"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="number" v-model="doc.meter" disabled></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder="" v-model="doc.name"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder="" v-model="doc.address"></div><div class="row"><label class="mt-3"> </label><div class="col-12"><select class="form-select form-select mb-3" v-model="doc.code"><option selected=""></option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div></div><div class="mb-3"><label class="form-label"></label><p>{{doc.debtText}}</p></div><div class="mb-3"><label class="form-label"></label><p>{{(doc.invoiceAmount??0).formatFull()}}</p></div><div class="mb-3"><label class="form-label"></label><div class="input-group mb-3"><input class="form-control" type="number" placeholder="" v-model.number="doc.paymentAmount"><span class="input-group-text"></span></div></div><div class="row mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/payment/list?searchparam=${($route.query??{}).searchparam}`"></router-link><button class="btn btn-success mx-2" type="submit" @click="submit()"></button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()"></a></div></form></div>