<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='')==-1"></NoAccess><div class="container-fluid p-4" v-else><h2 class="text-decoration-underline mb-4"></h2><button class="btn btn-outline-success text-dark" type="button" @click="downloadExcel()"> <span> Excel</span></button><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><div class="row d-print-none"><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.year"> <option selected="" :value="0"></option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model.number="search.month"> <option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div></div><div class="row d-print-none"><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.code"> <option selected=""></option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div><div class="col-6"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-xs example" v-model="search.cat"> <option selected=""></option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="1,"> 1</option><option value="2,"> 2</option><option value="3,"> 3</option><option value="4,"></option></select></div></div><div class="row d-flex align-items-start mt-5 table-print"><div class="col-sm-12 col-md-6"><div class="row"><h5> <span class="text-primary">({{(rates.find(el=>el.code==search.code)??{}).name}})</span></h5></div><div class="row"><h5> <span class="text-primary fw-bold">{{new Date(this.Date).toThaiShort()}}</span></h5></div><div class="row"><h5> <span class="text-primary"> {{getThaiMonth(search.month)}} {{search.year}}</span></h5></div></div><div class="col-sm-12 col-md-6"><div class="row"><h5 class="text-end">333  1  23     10900</h5></div><div class="row"><h5 class="text-end">: []</h5></div></div></div><div class="row"><div class="col"><span></span><span class="text-primary">{{(totalPayment??0).formatFull()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"> </span><span class="text-primary">{{inclVat(totalInvoice??0).formatFull()}} </span><span></span><span class="ms-3"> |</span><span class="ms-4"></span><span class="text-primary">{{inclVat((totalInvoice??0)+(totalDebt??0)).formatFull()}} </span><span></span></div></div><div class="row"><div class="col-12 p-2"><table class="table table-striped table-hover table-print"><thead class="table-dark"><tr class="text-center"><th class="align-middle"></th><th class="align-middle"></th><th class="align-middle">-</th><th class="align-middle"></th><th class="align-middle"></th><th class="align-middle"></th><th class="align-middle"></th><th class="align-middle text-end"> ()</th><th class="align-middle text-end"> ()</th><th class="align-middle text-end"></th><th class="align-middle text-end"> ()</th></tr></thead><tbody><tr class="text-center" v-for="item,i in docs"><th scope="row">{{i+1}}</th><td>{{item.meter}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.invoiceNumber}}</td><td>{{item.category}}</td><td>{{toThaiFormat(item.paymentDate)}}</td><td> <template v-if="item.month!=undefined&amp;&amp;item.year!=undefined"><span>{{getThaiMonth(item.month)}}{{(item.year).toString().substring(2, 4)}}</span></template></td><td class="text-end">{{(item.invoiceAmount??0).formatFull()}}</td><td class="text-end">{{(item.paymentAmount??0).formatFull()}}</td><td class="text-end"> <span>{{item.debtText}} <br/></span><span>{{inclVat(item.debtAmount,item.vatRate)}} </span></td><td class="text-end"> <span>{{inclVat((item.billAmount??0)+(item.debtAmount??0),item.vatRate).formatFull()}}</span></td></tr></tbody></table></div></div></div>