<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4"> Excel</h2><label>Excel</label><div class="row mb-3"></div><div class="input-group mb-3"><input class="form-control" type="file" label="Upload" @input="importExcel($event)"><button class="btn btn-outline-secondary" type="button"></button></div><div class="row"><div class="col"><label class="mt-3"> </label><label class="mt-3 text-danger"> </label><select class="form-select form-select mb-3" v-model="code"><option selected=""></option><option v-for="item in rates" :value="item.code">{{item.name}} ({{item.code}})</option></select></div></div><div class="row"><label class="mt-3">:</label><div class="col-6"><select class="form-select form-select-lg mb-3" v-model="category"><option selected=""></option><option>1</option><option>2</option><option>3</option><option>4</option></select></div><div class="col-6"><select class="form-select form-select-lg mb-3" v-model="categoryType"><option selected=""></option><option></option><option></option><option></option></select></div><div class="col-6"><select class="form-select form-select-lg mb-3" v-model.number="year"><option selected=""></option><option>2564</option><option>2563</option><option>2562</option><option>2561</option><option>2560</option></select></div><div class="col-6">   <select class="form-select form-select mb-3" v-model.number="month"> <option selected=""></option><option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div></div><div class="row mb-3"><table class="table table-striped table-hover"><thead><tr class="table-dark"><th>#</th><th>numberInit</th><th>meter</th><th>name</th><th>address</th><th>debtText</th><th>debtAmount</th><th>qty</th><th>rate</th></tr></thead><transition-group name="fade" tag="tbody"><tr v-for="doc,i in docs" :key="doc.number"><th scope="row">{{i+1}}</th><td>{{doc.sequence}}</td><td>{{doc.meter}}</td><td>{{doc.name}}</td><td>{{doc.address}}</td><td>{{doc.debtText}}</td><td>{{doc.debtAmount}}</td><td>{{doc.qty}}</td><td>{{doc.rate}}</td></tr></transition-group></table></div><div class="row mb-5"></div><div class="d-flex justify-content-center"> <button class="btn btn-secondary mx-2" type="button" @click="$router.go(-1)"></button><button class="btn btn-success mx-2" type="button" @click="postData()"></button></div></div>