<div class="container"><h1></h1><div class="row my-3"><div class="col-12"><label class="form-label"></label><input class="form-control" type="number" v-model="count"></div></div><div class="row"><div class="col-6"><h4></h4><ul class="list-group"><li class="list-group-item position-relative" v-for="item in debt" @click="item.isPaid = !item.isPaid"> <span>{{new Date(item.date).toThaiMonth()}}</span><span class="mx-2">{{new Date(item.date).getFullYear() + 543}} : </span><span class="text-danger">{{(item.amount??0).formatFull()}} </span><div class="form-check form-switch"><input class="form-check-input" type="checkbox" v-model="item.isPaid"><label class="form-check-label"></label></div><span class="position-absolute end-0 top-0 me-3"><span class="text-success" v-if="item.isPaid"></span><span class="text-warning" v-else></span></span></li></ul></div><div class="col-6"><h4>  1</h4><h6 class="text-info">()</h6><p>{{display1}}</p><h4>  2</h4><h6 class="text-info">()</h6><p>{{display2}}</p></div></div></div>