<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4"> {{(area.prefix??'')+(area.name??'')}}</h2><div class="row"><div class="col-3"><label></label><input class="form-control" type="number" v-model.number="doc.contractYear"></div><div class="col-3"><JMCalendar label="" v-model="doc.operationDate"> </JMCalendar></div><div class="col-3"><label></label><select class="form-select" v-model.number="doc.operationYear"><option selected=""></option><option v-for="item in doc.contractYear" :value="item">{{item}}</option></select></div></div><div class="row mb-5"><div class="col-3"><label></label><select class="form-select form-select-sm" v-model="copySource"><option v-for="item,i in doc.conditions" :value="i">{{i+1}}</option></select></div><div class="col-3"><label>...% ()</label><div class="input-group-sm d-flex"><input class="form-control" type="number" v-model="addPercent"><span class="input-group-text">%</span></div></div><div class="col-3"><label>... ()</label><div class="input-group-sm d-flex"><input class="form-control" type="number" v-model="addPeriod"><span class="input-group-text"></span></div></div><div class="col-3 mt-4"><a class="btn btn-secondary btn-sm" @click="copy()"></a></div></div><table class="table table-stipe"><thead><tr><th></th><th></th><td></td><td style="width: 15%"></td></tr></thead><tbody><tr v-for="(item,i) in doc.conditions"><td> <span>{{i+1}}</span><span v-if="doc.operationYear==i+1"> <i class="fas fa-pennant text-success ms-2"></i></span></td><td> <span v-if="i+1&gt;=doc.operationYear">{{dateRange(doc.operationDate,i)}}</span></td><td><div class="input-group d-flex"><div class="form-floating flex-fill me-2"><select class="form-select" v-model="item.category" @change="item.contributionPercent=0"><option></option><option>/</option><option></option><option></option></select><label></label></div><template v-if="item.category=='/'"><div class="form-floating me-1"><select class="form-select" v-model="item.profitType" @change="item.contributionPercent=0"><option value="1"></option><option value="2"></option></select><label></label></div><template v-if="item.profitType == 1"><div class="form-floating" style="width: 5rem"><input class="form-control" v-model="item.contributionPercent" type="number"><label></label></div><span class="input-group-text me-1">%</span></template><div class="form-floating me-1"><select class="form-select" v-model="item.lossType"><option value="1"> .</option><option value="2"> </option><option value="3"></option></select><label></label></div><div class="form-floating"><JMNumber v-model="item.contributionLimit" label=" ()"></JMNumber></div><span class="input-group-text me-1"></span></template><template v-if="item.category==''"><JMNumber v-model="item.contributionLimit" label=""></JMNumber><span class="input-group-text"></span></template><template v-if="item.category==''"><div class="form-floating" style="width: 5rem"><input class="form-control" v-model="item.contributionPercent" type="number"><label></label></div><span class="input-group-text me-1">%</span><JMNumber v-model="item.contributionLimit" label=" ()"></JMNumber><span class="input-group-text"></span></template></div></td><td> <div class="form-floating"><select class="form-select" v-model="item.period"><option></option><option></option><option></option><option></option></select><label></label></div></td></tr></tbody></table><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/condition/list"></router-link><button class="btn btn-success mx-2" type="button" @click="submit()"></button></div></form></div>