3 mt-5 col-5"><div class="card-body row"><canvas ref="chart1"></canvas><hr class="mt-3"><p class="card-text"></p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart2"></canvas><hr class="mt-3"><p class="card-text"></p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart3"></canvas><hr class="mt-3"><p class="card-text"></p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart4"></canvas><hr class="mt-3"><p class="card-text"></p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart5"></canvas><hr class="mt-3"><p class="card-text"></p></div></div><div class="card dashboard-card mx-3 mt-5 col-5"><div class="card-body row"><canvas ref="chart6"></canvas><hr class="mt-3"><p class="card-text"></p></div></div></div>