<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4"> {{(area.prefix??'')+(area.name??'')}}</h2><table class="table table-borderless tableFixHead"><thead><tr style="border-bottom:2px solid black;"><th>#</th><th></th><th></th><th></th><th></th><th></th><th> <i class="fas fa-ellipsis-h"></i></th></tr></thead><tbody><template v-for="(item,i) in docs"><tr><th>{{i+1}}  </th><td> <select class="form-select" v-model="item.category"><option></option><option></option><option></option><option></option></select></td><td><JMCalendar v-model="item.recordDate"></JMCalendar></td><td> <JMNumberLite v-model="item.amount" suffix=""></JMNumberLite></td><td> <select class="form-select form-select-xs mb-3" v-model.number="item.year" @change="checkMonthYear"> <option selected="" :value="0"></option><option>{{(new Date().getFullYear()+543)+1}}</option><template v-for="item,i in 10"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></td><td> <select class="form-select form-select-xs mb-3" aria-label=".form-select-lg example" v-model.number="item.month" @change="checkMonthYear"> <option selected="" :value="13"></option><option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></td><td><a class="btn btn-danger" @click="deleteData(item._id)"><i class="fas fa-trash-alt"></i></a></td></tr><tr style="border-bottom:1px solid grey;"><td colspan="3"><JMUploads v-model="item.fileUrl" folder="collection"></JMUploads></td><td colspan="4"><ul class="list-group"><li class="list-group-item" v-for="(el,k) in item.fileUrl"> <div class="d-flex justify-content-between"><span>{{el}}</span><a :href="`${filePath}/${el}`"><i class="fas fa-cloud me-3"></i><span></span></a><a class="btn btn-danger" @click="deleteUrl(item,k)"><i class="fas fa-trash-alt"></i></a></div></li></ul></td></tr></template></tbody></table><div class="d-flex justify-content-center mt-3 mb-5"><button class="btn btn-success text-white mx-2" type="button" @click="addItem()"><i class="fas fa-plus-circle me-2"></i><span></span></button></div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" type="button" to="/portal/collection/list"></router-link><button class="btn btn-success mx-2" type="button" @click="updateItems()"></button></div></form></div>