<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4" v-if="this.$route.params.id == undefined"></h2><h2 class="text-decoration-underline mb-4" v-else></h2><div class="mb-3 mt-5"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.code"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.name"></div><div class="mb-3"><label class="form-label"></label><select class="form-select" v-model.number="doc.category"><option :value="1"> 1:  (.,.)</option><option :value="2"> 2: </option><option :value="3"> 3: </option><option :value="4"> 4: </option></select></div><div class="row mb-5"> </div><div class="d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" :to="`/portal/code/list/`"></router-link><button class="btn btn-success mx-2" type="button" @click="submit()"></button><a class="btn btn-danger" v-if="$route.params.id!=undefined" @click="deleteData()"></a></div></form></div>