<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4"> 3</h2><div class="row justify-content-center"><div class="col-10 p-2 border border-4 border-light"><div class="mb-2 text-center">     <h5> - </h5><h5 class="ms-2 text-primary">{{(area.prefix??'')+(area.name??'')}}</h5></div><div class="mb-4 text-center">     <span> </span><span class="text-primary me-1">{{year}} </span><span class="text-primary">( {{quarter}})</span></div><table class="table"><thead><tr><th class="align-middle" colspan="2"></th><th class="text-end"><span class="me-4" v-if="isEndorsed">{{doc.oldText}}</span></th><th class="text-end" v-if="isEndorsed"><span class="me-4">{{doc.newText}}</span></th><th></th></tr></thead><tbody><template v-for="category,i in categories"><tr v-if="!showOnlyNonZeroes||((item.left??{}).totalAmount&gt;0||(item.right??{}).totalAmount&gt;0)"><th class="col-1 text-center">{{i+1}}             </th><th scope="row">{{category}}</th><th class="text-end" scope="row">{{((_.chain(union[i]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</th><th class="text-end" scope="row">{{((_.chain(union[i]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</th></tr></template><tr><th class="col-1 text-center"> </th><th scope="row"></th><th class="text-end border-bottom border-dark" scope="row"> <span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('left.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></th><th class="text-end border-bottom border-dark" scope="row"> <span v-if="union[0]==undefined||union[1]==undefined||union[2]==undefined||union[3]==undefined">-</span><span v-else>{{((_.chain([...union[0],...union[1],...union[2],...union[3]]).map('right.totalAmount')).reduce((sum,n)=>sum+(n??0),0).value()??0).formatFull()}}</span></th></tr><tr v-if="t