<div class="container-fluid"><div class="row"><div class="col-sm-12 col-md-6" style="min-height:90vh;max-height:95vh;"><a class="btn btn-outline-primary mb-3" @click="generate()"> <i class="fas fa-file-word me-2"></i><span> Words</span></a><div class="row"><div class="col d-flex justify-content-end mb-2"><div class="form-check ms-3"> <input class="form-check-input" type="checkbox" v-model="doc.showLeft" @change="generatePDF('preview')"><label class="form-check-label">. </label></div><div class="form-check ms-3"> <input class="form-check-input" type="checkbox" v-model="doc.showRight" @change="generatePDF('preview')"><label class="form-check-label">. </label></div></div></div><iframe :src="pdfBLOB" height="100%" width="100%"></iframe></div><div class="col-sm-12 col-md-6" style="min-height:90vh;max-height:95vh; overflow-y:scroll;"><section><div class="row center mt-5"><div class="col-lg-6 col-md-8 mx-auto text-center"><h1></h1></div><div class="mb-3"><label class="form-label"><!----></label><select class="form-select" v-model="doc.area" @change="fetchArea(); generatePDF('preview')"><option v-for="item in areas" :value="item._id">{{item.name}}</option></select></div><div class="mb-3"><label class="form-label me-2">  </label><input class="me-2" id="form-check-input" type="checkbox" name="showRef" v-model="doc.showRef"><input class="form-control" type="text" placeholder=" " v-model="area.contractNumber" @change="generatePDF('preview')"></div><div class="col-12 mt-2" v-if="$route.params.id==undefined"><h5>  </h5><label class="form-label me-2"></label><input class="me-2" id="form-check-input" type="checkbox" name="showAttachment" v-model="doc.showAttachment"><label class="text-danger">**  PDF </label><select class="form-select" v-model="calculation" @change="generatePDF('preview')"><option select="" :value="{ledgers:[]}"></option><template v-for="item in calculations"><option :value="item"> {{item.calendarYear}} / {{item.quarter!=0?` ${item.quarter}`:``}}</option></template></select></div><div class="col-12 mt-2" v-else><h5>  </h5><label class="form-label me-2"> {{calculation.calendarYear}}  {{calculation.quarter}}</label><input class="me-2" id="form-check-input" type="checkbox" name="showAttachment" v-model="doc.showAttachment"><label class="text-danger">**  PDF </label><select class="form-select" v-model="calculation" @change="generatePDF('preview')"><option select="" :value="{ledgers:[]}"></option><template v-for="item in calculations"><option :value="item"> {{item.calendarYear}} / {{item.quarter!=0?` ${item.quarter}`:``}}</option></template></select></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.documentNumber" @change="generatePDF('preview')"></div><div class="mb-2"><JMCalendar label="" v-model="doc.issueDate" @change="generatePDF('preview')"></JMCalendar></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.subject" @change="generatePDF('preview')"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.to" @change="generatePDF('preview')"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.approverName" @change="generatePDF('preview')"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.approverPosition" @change="generatePDF('preview')"></div><div class="mb-3"><label class="form-label"></label><input class="form-control" type="text" placeholder=" " v-model="doc.notifyTo" @change="generatePDF('preview')"></div><div class="mb-3"><label class="form-label"></label><JMRichText v-model="doc.content"></JMRichText></div><div class="mb-3"><label class="form-label">*</label><textarea class="form-control" style="height: 120px" v-model="doc.remark"></textarea></div><div class="mb-3"><label class="mb-0"></label><div class="input-group mb-3"><input class="form-control" type="file" @change="chooseSignature()" ref="signatureInput"></div><img src="" width="275" alt="Image preview..." ref="signatureImage"></div><div class="row"><table class="table"><thead class="thead-dark"><tr><th class="col-2" scope="col">#</th><th class="col-2" scope="col"></th><th></th><th class="col-2" scope="col">...</th></tr></thead><tbody><tr v-for="item,i in doc.attachments"><th scope="row">#</th><td><template v-if="doc.attachmentsName!=undefined"><input class="form-control" type="text" v-model="doc.attachmentsName[i]" @change="generatePDF('preview')"></template></td><td><span style="word-wrap:break-word;">{{item}}</span></td><td> <i class="fas fa-trash-alt text-danger" @click="doc.attachments.splice(i,1);doc.attachmentsName.splice(i,1)">  </i></td></tr></tbody></table></div><div class="mb-3 mt-3"><label class="form-label"></label><JMUploads v-model="doc.attachments" folder="coverLetter"></JMUploads><div class="table-container-scroll-x"></div></div><div class="mb-4"><label></label><JMUploads v-model="doc.documents">   </JMUploads><div class="col"><ul class="list-group"><li class="list-group-item" v-for="(el,k) in doc.documents"> <div class="d-flex justify-content-between"><span> </span><a :href="`${filePath}/${el}`"><i class="fas fa-cloud me-3"></i><span></span></a><a class="btn btn-danger" @click="deleteUrl(doc,k)"><i class="fas fa-trash-alt"></i></a></div></li></ul></div></div><div class="mb-5 d-flex justify-content-center"><router-link class="btn btn-secondary mx-2" :to="`/portal/cover/letter/list?searchparam=${$route.query.searchparam}`"></router-link><template v-if="!doc.isPrint"><button class="btn btn-outline-success mx-2" type="button" @click="submitData()"></button><button class="btn btn-success mx-2" type="button" @click="saveFinal()"></button></template></div></div></section></div></div></div>