<form class="mb-3" @submit.prevent="searchEmit"><div class="row" v-if="this.$route.path == '/portal/calculation/list' || this.$route.path == '/portal/estimation/list' || this.$route.path == '/portal/cover/letter/list'"><div class="col-4"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" v-model.number="search.year"> <option selected="" :value="0"></option><template v-for="item,i in 6"><option>{{(new Date().getFullYear()+543)-i}}</option></template></select></div><div class="col-4"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" id="quarter-search" v-model.number="search.year"> <option selected="" :value="0"></option><option :value="1">1</option><option :value="2">2</option><option :value="3">3</option><option :value="4">4</option></select></div><div class="col-4"><label class="mt-3"></label><select class="form-select form-select-xs mb-3" aria-label=".form-select-lg example" v-model.number="search.month"> <option selected="" :value="13"></option><option :value="1"></option><option :value="2"></option><option :value="3"></option><option :value="4"></option><option :value="5"></option><option :value="6"></option><option :value="7"></option><option :value="8"></option><option :value="9"></option><option :value="10"></option><option :value="11"></option><option :value="12"></option></select></div></div><div class="row" v-if="this.$route.path == '/portal/cover/letter/list'"><div class="col-6"><JMInput inputType="text" placeholder="" v-model="search.name" label=""></JMInput></div><div class="col-6"><JMInput inputType="text" placeholder="" v-model="search.name" label=""></JMInput><JMInput inputType="text" placeholder="" v-model="search.name" label=""></JMInput></div></div><div class="row" v-if="this.$route.path == '/portal/billing/group/list' || this.$route.path == '/portal/condition/list' || this.$route.path == '/portal/collection/list' || this.$route.path == '/portal/area/list' || this.$route.path == '/portal/area/plan/list'"><div class="col-12"><label class="form-label" for="searchForArea"></label><input class="form-control" id="searchForArea" type="search" autocomplete="on" placeholder="" v-model="search.name"></div></div><div class="row" v-if="this.$route.path == '/portal/code/list'"><div class="col-12"><JMInput inputType="text" placeholder="" v-model="search.code" label=""></JMInput></div></div><div class="row my-2"><div class="col d-flex align-items-center mt-1 justify-content-end"><button class="btn btn-outline-success mx-2" type="button" @click="emitClear()" v-if="isClear"></button><button class="btn btn-outline-success mx-2" type="button" @click="emitSearchClear()" v-else></button><button class="btn btn-outline-success" type="submit"></button></div></div></form>