<div class="container-fluid p-4"><h2 class="text-decoration-underline mb-4"> Excel</h2><label class="mb-0">Import Excel</label><div class="input-group mb-3"><input class="form-control" type="file" label="Upload" @input="importExcel($event)"></div><label class="mb-0">Import CSV</label><div class="input-group mb-3"><input class="form-control" type="file" label="Upload" @input="importCSV($event)"></div><form class="row" @submit.prevent="postData()"><div class="row mb-3"><table class="table table-striped table-hover"><thead><tr class="table-dark"><th scope="col"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th><th class="text-end" scope="col"></th></tr></thead><transition-group name="fade" tag="tbody"><tr v-for="doc,i in docs" :key="doc.number"><th scope="row">{{i+1}}</th><td> <span>{{new Date(doc.paymentDate).toThaiShort()}}</span></td><td> <span>{{doc.time}}</span></td><td>{{doc.invoiceNumber}}</td><td>{{doc.name}}</td><td>{{doc.address}}</td><td class="text-end">{{(doc.paymentAmount??0).formatFull()}}</td></tr></transition-group></table></div></form><div class="row mb-5"></div><div class="d-flex justify-content-center"> <router-link class="btn btn-secondary mx-2" :to="`/portal/payment/list/`"></router-link><button class="btn btn-success mx-2" type="button" @click="postData()"></button></div></div>