<div class="container-fluid p-4"><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><div class="row mb-2"><div class="col"><span> </span><span class="text-primary mx-2">{{(totalCount??0).formatComma()}} </span><span></span><span class="ms-1"> |</span><span class="ms-1"></span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"> </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"> </span><span class="text-primary mx-2">{{receipts.map(el=>el.vat).reduce((a,b)=>a+b,0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"></span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span></span></div></div><div class="row"><table class="table small-text-table"><thead><tr><th></th><td></td><td></td><td></td><td></td><td>-</td><td></td><td></td><td></td><td></td><td></td><td></td><td></td></tr></thead><tbody> <tr v-for="item,i in receipts"><th>{{i+1}}</th><td>{{item.invoiceNumber}}</td><td>{{item.meter}}</td><td>WMA-{{item.sequence}}</td><td>{{item.category}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.address}}</td><td>{{(item.debtAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{((item.billAmount*1.07)||0).formatFull()}}</td><td>{{(item.invoiceAmount??0).formatFull()}} </td><td>{{(item.invoiceAmount??0).formatFull()}} </td><td>{{(item.paymentAmount??0).formatFull()}}</td></tr></tbody></table></div><h5 class="strong"> . .</h5><p>. <span>|  1</span><span class="ms-2"></span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='1')??[]).length}} </span><span>|  2 </span><span class="ms-2"></span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='2')??[]).length}} </span><span>|  3</span><span class="ms-2"></span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.category==='3')??[]).length}} </span><span> ...</span><span> </span><span class="mx-3 text-primary">{{(totalAmount??0).formatFull()}}</span><span> ()</span></p><p class="ms-5"></p></div>