<NoAccess v-if="store.state.access.findIndex(el=&gt;el=='')==-1"></NoAccess><div v-else><div class="modal fade" id="receiptModal"><div class="modal-dialog modal-xl"><div class="modal-content"><div class="modal-header"><div class="row mb-2"><div class="col"><span></span><span class="text-primary mx-2">{{(totalCount??0).formatComma()}} </span><span></span><span class="ms-1"> |</span><span class="ms-1"></span><span class="text-primary mx-2">{{(totalPayment??0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"> </span><span class="text-primary mx-2">{{(totalAmount??0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"> </span><span class="text-primary mx-2">{{receipts.map(el=>el.vat).reduce((a,b)=>a+b,0).formatFull()}}</span><span></span><span class="ms-1"> |</span><span class="ms-1"></span><span class="text-primary mx-2">{{(totalDebt??0).formatFull()}}</span><span></span></div></div></div><div class="modal-body"><div class="row"><table class="table small-text-table"><thead><tr><th></th><td></td><td></td><td></td><td></td><td>-</td><td></td><td></td><td></td><td></td><td></td><td></td><td></td></tr></thead><tbody> <tr v-for="item,i in receipts"><th>{{i+1}} {{item.year}} {{item.month}}</th><td>{{item.invoiceNumber}}</td><td>{{item.meter}}</td><td> <span v-if="item.sequence.search('WMA-')==-1||item.sequence.search('wma-')==-1">WMA-</span><span>{{item.sequence}}</span></td><td>{{item.category}}</td><td>{{item.prefix??""}} {{item.name??"-"}}</td><td>{{item.address}}</td><td>{{(item.debtAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{((item.billAmount*1.07)||0).formatFull()}}</td><td>{{(item.invoiceAmount??0).formatFull()}}</td><td>{{(item.billAmount??0).formatFull()}}</td><td>{{(item.paymentAmount??0).formatFull()}}</td></tr></tbody></table></div><h5 class="strong"> . .</h5><p>. <span>|  1</span><span class="ms-2"></span><span class="mx-3 text-primary">{{(receipts.filter(el=>el.ca