m.to.category">{{item.from.category}}</li></ul></td><td> <ul class="list-group" v-if="item.from!=undefined &amp;&amp; item.to!=undefined"><li class="list-group-item-b" v-if="item.from.qty != item.to.qty">{{item.to.qty}}</li><li class="list-group-item-b" v-if="item.from.categoryType != item.to.categoryType">{{item.to.categoryType}}</li><li class="list-group-item-b" v-if="item.from.category != item.to.category">{{item.to.category}}</li></ul></td><td> <span>{{item.note}}</span></td><td><a :href="`${filePath}/${item.fileUrl}`">{{item.fileUrl}}</a></td><td><div class="d-flex align-items-center"><button class="btn btn-danger rounded m-2 btn-sm" type="button" @click="updateStatus(item,'')"> <i class="far fa-times-circle"></i><label></label></button></div></td><td><div class="d-flex align-items-center" v-if="item.requestType==''"><button class="btn btn-success rounded m-2 btn-sm" type="button" @click="editCoverLetter(item,'')"> <i class="far fa-check-circle mx-1"> </i><label></label></button></div><div class="d-flex align-items-center" v-else><button class="btn btn-success rounded m-2 btn-sm" type="button" @click="updateStatus(item,'')"> <i class="far fa-check-circle mx-1"> </i><label></label></button></div></td><td></td></tr></template></tbody></table></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)"></a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)"></a></li></ul></nav></div></div>