<div class="container-fluid p-4"><form class="col-12"><h2 class="text-decoration-underline mb-4 report-table-print hearder"> {{(area.prefix??'')+(area.name??'')}}</h2><div class="row d-print-none"><div class="col-3"><JMCalendar label="" v-model="doc.operationDate"> </JMCalendar></div><div class="col-3"><JMCalendar label="" v-model="area.contractEnd"> </JMCalendar></div><div class="col-3"><label class="form-label"></label><p> {{doc.operationYear}}</p></div><div class="col-3 align-self-end text-end"><input class="form-check-input me-2" type="checkbox" v-model="showDescription"><label class="form-check-label" for=""></label></div></div><div class="d-flex justify-content-end my-3 d-print-none"><button class="btn btn-primary text-white" @click="print()"></button></div><table class="table table-bordered report-table-print landscape"><thead><tr class="text-center"><th rowspan="2"></th><th rowspan="2"></th><th rowspan="2"></th><th colspan="3"> </th><th colspan="3"> </th><th colspan="3"> </th><th colspan="3"> </th><th rowspan="2" width="800" v-if="showDescription == true"></th></tr><tr class="text-center"><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th><th>..</th></tr></thead><tbody><tr><td class="text-center"><span>1</span></td><td class="text-center" colspan="2"><span>. </span></td><td colspan="12"></td><td v-if="showDescription == true"> <span> </span></td></tr><template v-for="(item,i) in doc.conditions"><template v-if="i+1&gt;=doc.operationYear"><tr><td class="text-center"><span>{{i+1}}</span><span v-if="doc.operationYear==i+1"> <i class="fas fa-pennant text-success ms-2"></i></span></td><td class="text-center"><div class="row text-center"><span> {{yearCollect(doc.operationDate,i).formatThai()}} </span><span class="text-muted small">({{(item.contributionLimit??0).formatFull()}} )</span></div></td><td class="text-end" v-if="item.contributionLimit == undefined "><span>{{zero.formatFull().formatThai()}}</span></td><td class="text-end" v-else><span v-if="i+1&gt;=doc.operationYear">{{(calc(doc,1,i)+calc(doc,2,i)+calc(doc,3,i)+calc(doc,4,i)).formatFull().formatThai()}}</span></td><template v-for="mo,j in [10,1,4,7]"><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,j+1,i)}}</span><p>{{(calcQuarter(mo,i-(mo>9?2:1))??0).formatFull().formatThai()}}</p></td></template><td v-if="showDescription == true"> <span class="text-decoration-underline"></span><p>{{getHowToCalc(i)}}</p></td></tr></template></template><tr><td class="text-center"><span>***</span></td><td class="text-center"><span v-if="doc.operationDate != undefined"> {{yearCollect(doc.operationDate,(doc.conditions??0).length).formatThai()}}</span><span v-else></span></td><td class="text-end"><span>{{((calcLast(1)??0)+(calcLast(2)??0)+(calcLast(3)??0)+(calcLast(4)??0)).formatFull().formatThai()}}</span></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,1,(doc.conditions??0).length)}}</span><p>{{(calcLast(1)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,2,(doc.conditions??0).length)}}</span><p>{{(calcLast(2)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,3,(doc.conditions??0).length)}}</span><p>{{(calcLast(3)??0).formatFull().formatThai()}}</p></td><td class="text-end" colspan="3"><span class="text-muted small">{{getRange(doc,4,(doc.conditions??0).length)}}</span><p>{{(calcLast(4)??0).formatFull().formatThai()}}</p></td><td v-if="showDescription == true"><span> <span v-if="this.area.contractEnd!=undefined">{{new Date(this.area.contractEnd).toThaiShort().formatThai()}}</span><span v-else>-</span></span></td></tr></tbody></table></form></div>