<!-- Button trigger modal--><!-- Modal--><NoAccess v-if="store.state.access.findIndex(el=&gt;el=='')==-1"></NoAccess><div v-else><div class="modal fade" id="debtModal" tabindex="-1" aria-labelledby="debtModalLabel" aria-hidden="true"><div class="modal-dialog modal-xl"><div class="modal-content"><div class="modal-header"><div class="d-flex justify-content-between"><h5 class="modal-title" id="debtModalLabel"></h5></div><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="row d-flex justify-content-center align-items-center py-3"></div><table class="table"><tbody><tr><td></td><td>{{(payment??0).formatFull()}}</td><td>(1)</td></tr><tr><td></td><td>{{(SumChecked??0).formatFull()}}</td><td>(2)</td></tr><tr :class="{'text-danger':((payment??0)-(SumChecked??0))&lt;0}"><td></td><td>{{((payment??0)-(SumChecked??0)).formatFull()}}</td><td>(1) - (2)</td></tr></tbody></table><div class="col d-flex justify-content-end mb-2"><div class="form-check"><input class="form-check-input" type="checkbox" v-model="showPaid"><label class="form-check-label"> </label></div></div><table class="table table-bordered"><tbody><tr v-for="item in DebtFilter"><td><div class="d-flex"><div class="col" style="width:50px;" v-if="!isCut"><div class="d-flex" v-if="!item.isPaid"><input type="checkbox" v-model="item.check"></div></div><div class="col text-text me-3"><template v-if="item.isPaid"><div class="text-success"></div></template><div class="text-danger" v-else></div></div><div class="col"> <span>{{toThaiFormat2(item.year,item.month)}}</span></div><div class="col"> <span>{{((item.totalAmount)*1.07??0).formatFull()}}</span></div><div class="col"> <span></span></div></div></td></tr></tbody></table><div class="row text-center mt-3 align-items-center py-3"><div class="col text-end"></div><div class="col"><div class="border py-1 px-2 text-end"><span>{{(debts.filter(el=>!el.isPaid).map(el=>(el.totalAmount??0)*el.vat+(el.rate*el.qty))??0).reduce((a,b)=>a+b,0).formatFull()}}</span></div></div><div class="col text-start"><div></div></div></div></div><div class="modal-footer"><template v-if="!isCut"><button class="btn btn-light" type="button" data-bs-dismiss="modal"></button><template v-if="((payment??0)-(SumChecked??0))&gt;0"><button class="btn btn-success" type="button" @click="patchInvoice()"></button></template></template></div></div></div></div><div class="modal fade" id="exampleReceiptModal" tabindex="-1" aria-labelledby="exampleReceiptModalLabel" aria-hidden="true"><div class="modal-dialog" style="width:30em"><div class="modal-content"><div class="modal-header"><h5 class="modal-title" id="exampleModalLabel"></h5><button class="btn-close" type="button" data-bs-dismiss="modal" aria-label="Close"></button></div><div class="modal-body"><div class="row mb-3"><div class="col text-center"> {{selected.length}} </div></div><div class="row"><div class="col d-flex justify-content-center"><div class="form-check"><input class="form-check-input" id="flexCheckDefault" type="checkbox" value=""><label class="form-check-label" for="flexCheckDefault"></label></div></div></div></div><div class="modal-footer"><div class="col d-flex justify-content-center"><button class="btn btn-light mx-3" type="button" data-bs-dismiss="modal" ref="cancelModal"></button><button class="btn btn-success" type="button" @click="postData()" target="_blank"></button></div></div></div></div></div><div class="container-fluid p-4"><StepBilling step="3"></StepBilling><h2 class="text-decoration-underline mb-4"></h2><div class="col-12 d-flex justify-content-end"> <router-link class="btn btn-info me-3" to="/portal/payment/form"> (Key-In)</router-link><router-link class="btn btn-success text-white me-3" to="/portal/payment/import"> excel</router-link><router-link class="btn btn-success text-white" to="/portal/payment/import-ktb"> KTB</router-link></div><JMBillingsearch @search="fetchData()" @clear="docs = []; totalCount=0; totalPayment=0; totalBill=0; totalDebt=0;" v-model="search" isClear></JMBillingsearch><div class="row"><label></label><div class="col-2 d-flex justify-content-start align-items-start">  <select class="form-select form-select-sm mb-2" aria-label=".form-select-lg example" v-model="show" @change="fetchData(),clearPage()"> <option selected="" :value="999999999"></option><option :value="50">50</option></select></div><div class="col d-flex justify-content-end mb-2"> <button class="btn btn-danger text-white mx-3" v-if="selected.length != 0" type="button" @click="deleteMany()"></button><button class="btn btn-outline-success text-dark" type="button" :disabled="(selected.length == 0)" @click="downloadExcel()"> <span> Excel ({{selected.length}}) </span></button><button class="btn btn-primary text-white mx-3" v-if="selected.length == 0" type="button" disabled=""> ({{selected.length}}) </button><button class="btn btn-warning text-white mx-3" v-if="selected.length != 0" type="button" @click="postSum()"> ({{selected.length>0?1:0}})  </button><button class="btn btn-primary text-white mx-3" v-if="selected.length != 0" ref="callModal" type="button" data-bs-toggle="modal" data-bs-target="#exampleReceiptModal"> ({{selected.length}})  </button></div></div><div class="row mt-2"><div class="d-flex justify-content-center"><transition name="fade"><div class="col text-center" v-if="loading"><div class="spinner-border text-primary mx-3" role="status"></div><span></span></div></transition></div></div><div class="row mt-2"><div class="col"><span> </span><span class="text-primary" id="totalCount">{{(totalCount??0).formatComma()}} </span><span> </span><span class="ms-3"> |</span><span class="ms-4 mx-1"> </span><span class="text-primary">{{(totalPayment??0).formatFull()}}</span><span> </span><span class="ms-3"> |</span><span class="ms-4 mx-1"></span><span class="text-primary">{{(totalInvoice??0).formatFull()}}</span><span> </span><span class="mx-3">|</span><span>: </span><span class="text-primary">{{getThaiMonth(latest.month)}} {{(latest.year??0).toString().substring(2, 4)}}</span></div></div><table class="table table-striped"><thead class="table-dark"><tr class="text-center"><th><input type="checkbox" @change="selectAll($event)"></th><th></th><th></th><th></th><th></th><th></th><th></th><th></th><th class="text-end">  ()</th><th class="text-end">  ()</th><th width="100"> </th></tr></thead><tbody><template v-for="item,i in docs"><tr><td><div class="row text-center"><input type="checkbox" v-model="selected" :value="item._id"><i class="fas fa-receipt text-success mt-2" v-if="item.isNextStage" data-toggle="tooltip" data-placement="top" title="!"></i></div></td><td class="text-center" scope="row">{{(page*show)+(i+1)}}</td><td class="text-start"><router-link class="card-link" :to="`/portal/meter/history/${item.meter}`">{{item.meter}}</router-link></td><td class="text-start">{{item.name}}</td><td class="text-start">{{item.invoiceNumber}}</td><td class="text-center">{{item.category}}</td><td class="text-center">{{toThaiFormat(item.paymentDate)}}</td><td class="text-center"> <template v-if="item.month!=undefined&amp;&amp;item.year!=undefined"><span>{{getThaiMonth(item.month)}}{{(item.year).toString().substring(2, 4)}}</span></template></td><td class="text-end"> <span>{{(item.invoiceAmount).formatFull()}}</span></td><td class="text-end"> <i class="fas fa-user text-danger" v-if="(item.paymentAmount)&lt;calcTotal(item)" data-toggle="tooltip" data-placement="top" title="!"></i><span>{{(item.paymentAmount??0).formatFull()}}</span></td><td><div class="d-flex align-items-center justify-content-center"><template v-if="!item.process"><button class="btn btn-outline-primary btn-sm me-1" ref="callModal" type="button" data-bs-toggle="modal" data-bs-target="#debtModal" @click="id=item._id; isCut=item.isCut??false; getDebt(item.meter,item.paymentAmount)"></button></template><router-link class="btn btn-warning btn-sm" :to="`/portal/payment/edit/${item._id}?searchparam=${JSON.stringify(search)}`"></router-link></div></td></tr></template></tbody></table></div><div class="row"><div class="col"></div><nav aria-label="Page navigation example"><ul class="pagination justify-content-center"><li class="page-item disabled"><a class="page-link" href="#" @click="updatePaginate(page-1)"></a></li><li class="page-item" v-for="(item,index) in paginate" :class="{'active':(index==page)}"><a class="page-link" href="#" @click="updatePaginate(index)" v-if="index&gt;page-limit&amp;&amp;index&lt;page+limit">{{index+1}}</a></li><li class="page-item"><a class="page-link" href="#" @click="updatePaginate(page+1)"></a></li></ul></nav></div></div>